








import Vue from 'vue'

import { Tile, TileTypes } from '../map'

export default Vue.extend({
  props: ["cell", 'player'],
  computed: {
    style() {
      if (this.player) {
        return 'player'
      } else if (this.cell.isWall()) {
        return 'wall'
      } else if (this.cell.isDoor()) {
        return 'door'
      }

      return 'floor'
    },
    symbol():any {
      if (this.player) {
        return ''
      } else if (this.cell.display == ' ') {
        return ''
      } else if (this.cell.display == '+') {
        return ''
      } else if (this.cell.display == '#') {
        return ''
      } else {
        return 'E'
      }
    }
  }
})
