











































import Vue from 'vue'
import Component from 'vue-class-component'
import * as _ from 'lodash'

import Cell from './Cell.vue'
import Scene from './Scene.vue'

import { LevelMap } from '../map'
import { Walker } from '../creatures/walker'
import { Memory, MemoryTile } from '../creature'
import { generate } from '../generator/dungeon'
import { addDoors } from '../generator/post'

import { Chaser, Waiter, Explorer } from '../ai'

export default Vue.extend({
  data() {
    return {
      map: new LevelMap([[]]),
      radius: 10,
      ts: Date.now(),
      generatorOptions: {
        addDoors: false,
        minSize: 3,
        maxSize: 7,
        roomsCount: 10,
      }
    }
  },
  components: {
    Cell,
    Scene
  },
  methods: {
    generateMap() {
      this.map = this.buildMap()
      let x = 1,
          y = 1

      while (!this.map.visibleThrough(x, y)) {
        if (x < this.map.width - 1) {
          x += 1
        } else {
          x = 1
          y += 1
        }
      }

      new Walker(
        x,
        y,
        this.radius,
        this.map,
        new Explorer(),
      )

      new Walker(
        x + 2,
        y + 2,
        this.radius,
        this.map,
        new Chaser(),
      )

      x = this.map.width - 1
      y = this.map.height -1

      while (!this.map.visibleThrough(x, y)) {
        if (x > 1) {
          x -= 1
        } else {
          x = this.map.width - 1
          y -= 1
        }
      }

      new Walker(
        x - 1,
        y - 1,
        this.radius,
        this.map,
        new Explorer(),
      )

      new Walker(
        x,
        y,
        this.radius,
        this.map,
        new Chaser(),
      )
    },
    buildMap() {
      let map = generate(
        50,
        50,
        this.generatorOptions.minSize,
        this.generatorOptions.maxSize,
        this.generatorOptions.roomsCount,
      )

      if (this.generatorOptions.addDoors) {
        return addDoors(map)
      }

      return map
    }
  },
  created() {
    this.generateMap()
  }
})
